<template>
  <div id="notebook-list" class="detail">
    <header>
      <a href="#" class='btn' @click.stop.prevent="onCreate"><i class="iconfont icon-plus"></i> </a>
    </header>
    <main>
      <div class="layout">
        <h3>({{notebooks.length}})</h3>
        <div class="book-list">
          <router-link :to="`/note?notebookId=${notebook.id}`" class="notebook" v-for="(notebook,index) in notebooks" v-bind:key="index">
            <div>
              <span class="iconfont icon-notebook"></span> {{notebook.title}} <span>{{notebook.noteCounts}}</span><span class="action" @click.stop.prevent="onEdit(notebook)"></span><span class="action" @click.stop.prevent="onDelete(notebook)"></span><span class="data">{{notebook.createdAtFriendly}}</span>
            </div>
          </router-link>
          
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Auth from "@/apis/auth"
import Notebooks from "@/apis/notebook"
import {friendlyDate} from '@/helpers/util'


export default {
  name: 'NotebookList',
  data () {
    return {
      notebooks: []
    }
  },
  created(){
    Auth.getInfo()
      .then(res => {
        if(!res.isLogin){
          this.$router.push({path:'/login'})
        }
        
      }
    )

    Notebooks.getAll()
      .then(res =>{
        this.notebooks = res.data
      })
  },
  methods: {
    onCreate(){

      this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^.{1,30}$/,
          inputErrorMessage: '30'
        }).then(({ value }) => {
          return Notebooks.addNotebook({title:value})
          
        }).then(res=>{
          console.log(res)
          res.data.friendlyCreatedAt = friendlyDate(res.data.createdAt)
          
          this.notebooks.unshift(res.data)
       
          this.$message({
            type: 'success',
            message: res.msg
          })      
        })
    },

    
    onEdit(notebook){
        let title = ''
      this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          inputPattern: /^.{1,30}$/,
          inputValue:notebook.title,
          inputErrorMessage: '30'
        }).then(({ value }) => {
          title = value
          return Notebooks.updateNotebook(notebook.id,{title})
          
        }).then(res=>{
          notebook.title=title
       
          this.$message({
            type: 'success',
            message: res.msg
          })      
        })
    },
    onDelete(notebook){

      this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(()=>{
          return  Notebooks.deleteNotebook(notebook.id)
        }).then(res=>{
            this.notebooks.splice(this.notebooks.indexOf(notebook),1)
            this.$message({
            type: 'success',
            message: res.msg
          }) 
        })
    }
  }
}


</script>

<style lang='less' scoped>
@import url(../assets/css/notebook-list.less);
</style>


