<template>
  <div id="login">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="main">
            <pikachu></pikachu>

          </div>
          <div class="form">


            <h3 v-on:click="showRegister()"></h3>
            <div v-bind:class="{show:isShowRegister}" class="register">
              <input type="text" v-model="register.username" placeholder="">
              <input type="password" v-model="register.password" @keyup.enter="onRegister" placeholder="">
              <p v-bind:class='{error:register.isError}'>{{register.notice}}</p>
              <div class="button" @click="onRegister"></div>
            </div>


            <h3 @click="showLogin()"></h3>
            <div v-bind:class="{show:isShowLogin}" class="login">
              <input type="text" v-model='login.username' placeholder="">
              <input type="password" v-model='login.password' @keyup.enter="onLogin"
               placeholder="">
              <p v-bind:class='{error:login.isError}'>{{login.notice}}</p>
              <div class="button" @click="onLogin"></div>



            </div>
            
          </div>
        </div>
      </div>
    </div>

  </div>
  
</template>

<script>
// import request from '@/helpers/request'
import Auth from "@/apis/auth";
import Bus from "@/helpers/bus";
import pikachu from "@/components/Pikachu";
// Auth.getInfo()
//   .then(data=> {
//     console.log(data)
//   })
// request('/auth')
// .then(
//   data=>{console.log(data)}
//   )

export default {
  name: "Login",
  components: {
    pikachu
  },
  data() {
    return {
      isShowLogin: true,
      isShowRegister: false,
      login: {
        username: "",
        password: "",
        notice: "",
        isError: false
      },
      register: {
        username: "",
        password: "",
        notice: "",
        isError: false
      }
    };
  },

  methods: {
    showLogin() {
      this.isShowLogin = true;
      this.isShowRegister = false;
    },
    showRegister() {
      this.isShowLogin = false;
      this.isShowRegister = true;
    },
    onRegister() {
      if (!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
        this.register.isError = true;
        this.register.notice = "3~15";
        return;
      }
      if (!/^.{6,16}$/.test(this.register.password)) {
        this.register.isError = true;
        this.register.notice = "6~16";
        return;
      }

      Auth.register({
        username: this.register.username,
        password: this.register.password
      })
        .then(data => {
          console.log(data);
          this.register.isError = false;
          this.register.notice = "";
          this.$router.push({ path: "notebooks" });
          Bus.$emit("userInfo", { username: this.register.username });
        })
        .catch(data => {
          this.register.isError = true;
          this.register.notice = data.msg;
          console.log(data);
        });
    },
    onLogin() {
      if (!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username)) {
        this.login.isError = true;
        this.login.notice = "3~15";
        return;
      }
      if (!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true;
        this.login.notice = "6~16";
        return;
      }

      Auth.login({
        username: this.login.username,
        password: this.login.password
      })
        .then(data => {
          console.log(data);
          this.login.isError = false;
          this.login.notice = "";
          this.$router.push({ path: "notebooks" });
          Bus.$emit("userInfo", { username: this.login.username });
          console.log("");
        })
        .catch(data => {
          console.log(data);
          this.login.isError = true;
          this.login.notice = data.msg;
        });
      // request('/auth/login', 'POST',
      // {username:this.login.username,
      // password:this.login.password
      // }).then(data=>{console.log(data)})

      // console.log(`start login..., username: ${this.login.username} , password: ${this.login.username}`)
    }
  }
};

