</script>

<style lang='less' scoped>
.modal-mask {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background-color: rgba(0, 0, 0, 0.7);

  .modal-container {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 800px;
    height: 500px;
    
    border-radius: 25px;
    overflow: hidden;

    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2),
      0 6px 20px 0 rgba(0, 0, 0, 0.19);

    // .main {
    //   // flex: 1;
    //   border-radius: 15px 0 0 15px;

    //   // background: #36bc64
    //   //   url(//cloud.hunger-valley.com/17-12-13/38476998.jpg-middle) center
    //   //   center no-repeat;
    //   // background-size: contain;
    // }
    .form {
      width: 270px;
      // border-left: 1px solid #ccc;
      background-color: #fff;

      h3 {
        padding: 15px 20px;
        font-weight: normal;
        font-size: 16px;
        // border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
      }
      h3:nth-of-type(2) {
        border-top: 1px solid #eee;
      }
      .register,
      .login {
        padding: 0px 20px;
        height: 0;
        overflow: hidden;
        transition: height 0.4s;
        &.show {
          height: 193px;
        }

        input {
          display: block;
          width: 100%;
          height: 35px;
          line-height: 35px;
          padding: 0 6px;
          border-radius: 4px;
          border: 1px solid #ccc;
          font-size: 14px;
          margin-top: 10px;
        }
        input:focus {
          border: 3px solid #9dcaf8;
        }
        p {
          font-size: 12px;
          margin-top: 10px;
          color: #444;
        }
        .button {
          background-color: #364f6b;
          height: 36px;
          line-height: 36px;
          text-align: center;
          font-weight: bold;
          color: #fff;
          border-radius: 4px;
          margin-top: 18px;
          cursor: pointer;
        }
      }

      .login,
      .register .error {
        color: red;
      }
    }
  }
}
</style>


