<template>
  <div id="trash-detail">
    
    <h1>noteId : {{ $route.query.noteId }}</h1>
  </div>
</template>

<script>
import Auth from "@/apis/auth"

export default {
  name: 'TrashDetail',
  data () {
    return {
      msg: ''
    }
  },
  created(){
    Auth.getInfo()
      .then(res => {
        if(!res.isLogin){
          this.$router.push({path:'/login'})
        }
      })
  }
}
</script>

<style scoped>
h1 {
  color: blue;
}
</style>


