<template>
  <div id="note">
    <NoteSidebar @update:notes='val => notes = val'></NoteSidebar>
    <div class="note-detail">
      <div class="note-empty" v-show='!curNote.id'></div>
      <div v-show='curNote.id' class="ed">
        <div class="note-bar">
          <span>  {{curNote.createdAtFriendly}}</span>
          <span>  {{curNote.updatedAtFriendly}}</span>
          <span>{{statusText}}</span>
          <span class='iconfont icon-delete' @click="deleteNote"></span>
          <span class='iconfont icon-fullscreen' @click="isShowPreview=!isShowPreview"></span>
        </div>
        <div class="note-title">
        <input type="text" v-model="curNote.title" @input='updateNote' @keydown="statusText = '...'" placeholder="">

        </div>
        <div class="editor">
          <textarea v-show='!isShowPreview' v-model="curNote.content"  @input='updateNote' @keydown="statusText = '...'" placeholder=" markdown" ></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="isShowPreview" >
            
          </div>
        </div>
      </div> 
    </div>
  </div>
</template>

<script>
import NoteSidebar from '@/components/NoteSidebar'
import Notes from "@/apis/notes"
import Auth from "@/apis/auth"
import Bus from '@/helpers/bus'
import _ from "lodash";
//

import MarkdownIt from "markdown-it";
let md = new MarkdownIt();


export default {
  name: 'NoteDetail',
  components: {
    NoteSidebar
  },
  data () {
    return {
      curNote:{},
      notes:[],
      statusText: "",
      isShowPreview: false
    }
  },
  created(){
    Auth.getInfo()
      .then(res => {
        if(!res.isLogin){
          this.$router.push({path:'/login'})
        }
      })
    Bus.$once('update:notes',val =>{
      this.curNote = val.find(note => note.id == this.$route.query.noteId) ||{}
    })
  },
  beforeRouteUpdate(to, from, next){
    this.curNote = this.notes.find(note =>note.id == to.query.noteId) ||{}
    next()

  },
  methods: {
    
    //300ms
    updateNote: _.debounce(function() {
      Notes.updateNote({noteId: this.curNote.id},
        {title: this.curNote.title,
        content: this.curNote.content
      })
        .then(data => {
          this.statusText = "";
        })
        .catch(data => {
          this.statusText = "";
        });
    }, 300),
    deleteNote() {
      Notes.deleteNote({ noteId: this.curNote.id }).then(data => {
        // this.$message.seccess(data.msg)
        this.notes.splice(this.notes.indexOf(this.curNote),1)
        this.$router.replace({ path: "/note" });
      });
    }
  },
  computed: {
    
    previewContent() {
      return md.render(this.curNote.content || "");
    }
  },
}
</script>

<style lang='less' >
@import url(../assets/css/note-detail.less);
#note{
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex:1;
}
</style>


